import React from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../../Images/IconPack";

import InputLabel from "@material-ui/core/InputLabel/InputLabel";

import TextField from "@material-ui/core/TextField/TextField";
import { FormattedMessage } from 'react-intl';

const CorporateCost = (props) => {
    const [searchValue, setSearchValue] = React.useState('');
    const [dateReverse, setDateReverse] = React.useState(false);



    const getDayRow = (type) => {
        let date = new Date();
        var month = ["Jan", "Feb", "Mar", "Apr", "May", "Jun",
            "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"][date.getMonth()];
        let day;
        switch (type) {
            case 'Today':
                day = date.getDate();
                break;
            case 'Yesterday':
                day = date.getDate() - 1;
                break;
            default:
                break;
        }

        return (<TableRow className="date-row">
            <TableCell className="padding-sides-20 color-white" colspan="6">
                <b className="color-white">{type}</b>
                <span className="color-grey inline">{day} {month}</span>
            </TableCell>
        </TableRow>);
    }


    const getTbodyData = (reversed) => {

        if(!reversed){
            return (<>
                {getDayRow('Today')}

                {props.costsData.map((i, key) => {
                    return itemHTML(i, key);
                })}

                {getDayRow('Yesterday')}

                {props.costsData.map((i, key) => {
                    return itemHTML(i, key);
                })}

            </>);
        }else {
            return (<>

                {getDayRow('Yesterday')}

                {props.costsData.map((i, key) => {
                    return itemHTML(i, key);
                }).reverse()}


                {getDayRow('Today')}

                {props.costsData.map((i, key) => {
                        return itemHTML(i, key);
                    }).reverse()}

            </>);
        }
    };




    const prepearAmount = (amount, type) => {
        switch (type) {
            case 'loss':
                return (
                    <span className="red">{amount}</span>)
                break;
            case 'profit':
                return (<span className="green">{amount}</span>)
                break;
            default:
                return amount
                break;
        }
    }


    const itemHTML = (i, key) => {
        return  (   <TableRow key={key}>
            <TableCell className="padding-sides-25 red text-align-right">${i.amount}</TableCell>
            <TableCell className="padding-sides-15 color-white"><span className="icon-inside inline align-items-baseline">{IconPack.userIcons.forTable}</span>{i.user}</TableCell>
            <TableCell className="padding-sides-10 color-white">{i.costAction}</TableCell>
            <TableCell className="padding-sides-20 color-white">{i.costDetail[0]}
                <span className="ml-10 icon-inside inline align-items-baseline">{IconPack.licenseIcons.cog} {i.costDetail[1]}</span>
            </TableCell>
            <TableCell className="padding-sides-20 color-white text-align-right">{i.boughtLeft}</TableCell>
            <TableCell className="padding-sides-15 color-grey">3 Apr, 19:20:55</TableCell>
        </TableRow>);
    }




    let searchedData = props.costsData.filter(i => {

        if (i.user.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.costAction.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.costDetail[0].toLowerCase().includes(searchValue.toLowerCase()) ||
            i.costDetail[1].toLowerCase().includes(searchValue.toLowerCase()) ||
            i.boughtLeft.toLowerCase().includes(searchValue.toLowerCase())
          ) {
            return i
        }

    })

    searchedData = searchedData.map((i, key) => {

        return itemHTML(i, key);
    });


    /* active */
    return (
        <div className="ÑustomStyle">
            <div className="filterPanel">

                <FormattedMessage  id='filterPanel.search'>
                    {(msg) => ( <TextField
                        variant="filled"
                        fullWidth
                        className="search_area"
                        placeholder={msg}
                        value={searchValue}
                        onChange={(e)=>{setSearchValue(e.target.value)}}
                    >
                    </TextField>)}
                </FormattedMessage>

                <div className="search_additional_components">
                    <div className="calendar">{IconPack.searchComponents.calendar}</div>
                </div>
            </div>
            <Table className="configTable">
                <TableHead>
                    <TableRow>
                        <TableCell className="padding-sides-25 w125 text-align-right"><FormattedMessage id='table.amount' /></TableCell>
                        <TableCell className="padding-sides-15 w150"><FormattedMessage id='table.user' /></TableCell>
                        <TableCell className="padding-sides-10 w120"><FormattedMessage id='table.costAction' /></TableCell>
                        <TableCell className="padding-sides-20 "><FormattedMessage id='table.costDetail' /></TableCell>
                        <TableCell className="padding-sides-20 w125 text-align-right"><FormattedMessage id='table.boughtLeft' /></TableCell>
                        <TableCell className={dateReverse ? 'padding-sides-15 w125 td-date' : 'padding-sides-15 w125 td-date rotate-child-180 '} onClick={()=>{setDateReverse(!dateReverse)}}>{IconPack.tableIcons.date} <FormattedMessage id='table.date' /></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody  className="no-hover">
                    {searchValue.length ? searchedData : getTbodyData(dateReverse) }
                </TableBody>
            </Table>
        </div>
    );
}

const mapStateToProps = (state) => ({
    costsData: state.corporateLicenseCosts.costs,
});


export default connect(mapStateToProps)(CorporateCost);